<template>
  <div id="app">
    <header>
      <div class="header-container">
        <div class="mylogo">
          <h1>RALP DEXTER BONGATO</h1>
        </div>
        <div class="navs-container" :class="BurgerIsActive==true?'active':''">
          <router-link to="/">Home</router-link>
          <router-link to="/about">About</router-link>
          <router-link to="/skills">Skills</router-link>
          <router-link to="/projects">Projects</router-link>
          <router-link to="/contact">Contact</router-link>
        </div>
        <div class="burger-btn" v-on:click="BurgerIsActive=!BurgerIsActive,Loaded=true">
          <div class="burger-line1" :class="[BurgerIsActive==true?'active':'']">
          </div>
          <div class="burger-line2" :class="[BurgerIsActive==true?'active':'']">
          </div>
          <div class="burger-line3" :class="[BurgerIsActive==true?'active':'']">
          </div>
        </div>
      </div>
      <div class="mobile-nav":class="[BurgerIsActive==true?'active animated bounceInUp':'inactive animated bounceOutDown']" v-on:click="BurgerIsActive=!BurgerIsActive" v-if="Loaded==true">
        <div class="mobile-navs-container" :class="BurgerIsActive==true?'active':''" v-on:click="BurgerIsActive=!BurgerIsActive">
          <router-link to="/"><i class="fa fa-home"></i> Home</router-link>
          <router-link to="/about"><i class="fa fa-info-circle"></i> About</router-link>
          <router-link to="/skills"><i class="fa fa-css3"></i> Skills</router-link>
          <router-link to="/projects"><i class="fa fa-desktop"></i> Projects</router-link>
          <router-link to="/contact"><i class="fa fa-phone"></i> Contact</router-link>
        </div>
      </div>
    </header>
      <div class="content-container">
        <router-view>
        </router-view>
      </div>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      BurgerIsActive: false,
      Loaded: false
    }
  }
}
</script>

<style>
@import './assets/css/mycss.css';
@import 'https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css';
@import 'https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.min.css';
</style>



